.cases-block {
  //  width: 100%;
  //  height: 750px;
    display: flex;
    flex-direction: row-reverse;
    gap: 30px;
 //   flex-direction: row;

    @media (max-width: 1440px) {
        display: block;
        padding: 7.05rem 7.552% 7.1rem 6.4583%;
     //  flex-direction: column;
      //  justify-content: center;
    }
    @media (max-width: 500px) {
        padding: 0;
    }

    .vertical-nav {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
     //   background: var(--color-wight);

        @media (max-width: 1440px) {
            margin-top: 20px;
        }

        .pagination {
            display: flex;
            flex-direction: column;
            row-gap: 30px;
            align-items: center;

            @media (max-width: 1440px) {
                flex-direction: row;
                justify-content: center;
            }

            .swiper-pagination-bullet {
                display: flex;
                align-items: center;
                justify-content: center;
                opacity: 1;
                font-size: 32px;
                width: 70px;
                height: 70px;
                transition: all 0.3s;
                background-color: transparent;

                @media (max-width: 1024px) {
                    font-size: 24px;
                    width: 50px;
                    height: 50px;
                }

                &.swiper-pagination-bullet-active {
                    border: 1px solid var(--color-black);
                    border-radius: 100%;
                }
            }
        }

        @media (max-width: 1600px) {
            min-width: 110px;
        }
      //  background-color: var(--light-color);
      ////  padding: 13rem 5.2%;
      //  display: flex;
      //  flex-direction: column;
      //  align-items: center;
      //  justify-content: center;
      //  gap: 3.3rem;
      //  width: 14.1%;

        .nav-item {
            font-size: 1.6rem;
            font-weight: 400;
            line-height: 1.8785rem;
            color: var(--dark-color);
            border: 1px solid var(--light-color);
            border-radius: 50%;
            text-align: center;
            padding: 0.8rem 0.8rem;
            vertical-align: middle;
            cursor: pointer;

            &.active {
                border-color: var(--dark-color);
                animation: moveCircle 0.6s ease-in-out forwards;

                @keyframes moveCircle {
                    0% {
                        transform: scale(1);
                    }

                    50% {
                        transform: scale(1.1);
                    }

                    100% {
                        transform: scale(1);
                    }
                }
            }
        }

        @media (max-width: 1024px) {
            display: none;
        }
    }

    .content {
        display: flex;
        flex-direction: column;
      //  width: 85.9%;
        background: linear-gradient(
            111.75deg,
            var(--gradient-color) 0%,
            var(--light-color) 100.41%
        );
      //  padding: 1rem 7% 2rem 4%;
        transition: background 600ms ease;
        overflow-x: hidden;

        @media (max-width: 1024px) {
            width: 100%;
        }

        //.case-item {
        //    flex: 1 1 auto;
        //    width: 100%;

            .title {
                font-size: 3.8rem;
                font-weight: 600;
                line-height: 4.461rem;
                color: var(--light-color);
                width: 100%;
                text-align: left;
                margin-left: 50px;
                margin-top: 20px;
                margin-bottom: 2rem;

                @media (max-width: 1024px) {
                    font-size: 2.5rem;
                    line-height: 3rem;
                }

                @media (max-width: 768px) {
                    font-size: 1.8rem;
                    line-height: 2.2rem;
                    margin-bottom: 2rem;
                }
                @media (max-width: 500px) {
                    margin-left: 20px;
                    margin-top: 20px;
                }
            }

            .case-container {
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;

                .arrow {
                    margin-left: 2rem;
                    width: 10rem;
                    height: auto;
                    cursor: pointer;

                    @media (max-width: 1024px) {
                        display: none;
                    }
                }

                .case-enter {
                    opacity: 0;
                    transform: translateX(100%);
                }

                .case-enter-active {
                    opacity: 1;
                    transform: translateX(0);
                    transition:
                        opacity 600ms,
                        transform 600ms;
                }

                .case-exit {
                    opacity: 1;
                    transform: translateX(0);
                }

                .case-exit-active {
                    opacity: 0;
                    transform: translateX(-100%);
                    transition:
                        opacity 600ms,
                        transform 600ms;
                }
            }
        //}

        .case-footer {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            gap: 3%;
            margin-top: 2rem;
            padding-left: 4%;

            .status-line {
                width: 40.5%;
                height: 2px;
                border: none;
                display: flex;
                flex-direction: row;

                .line {
                    width: 25%;
                    height: 100%;
                    background-color: var(--dark-color);
                    transition: background-color 600ms ease;

                    &.active {
                        background-color: var(--light-color);
                    }
                }

                @media (max-width: 1024px) {
                    display: none;
                }
            }

            .circles {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                width: 17.5%;

                @media (max-width: 1024px) {
                    width: 20%;
                }
                @media (max-width: 425px) {
                    width: 25%;
                }

                .circle {
                    width: 1.75rem;
                    height: 1.75rem;
                    border-radius: 50%;
                    background-color: transparent;
                    border: 1px solid var(--dark-color);
                    transition:
                        background-color 0.3s,
                        transform 0.3s;
                    box-sizing: content-box;
                    cursor: pointer;

                    @media (max-width: 768px) {
                        width: 1.25rem;
                        height: 1.25rem;
                    }

                    &.active {
                        background-color: var(--dark-color);
                        animation: moveCircle 0.6s ease-in-out forwards;

                        @keyframes moveCircle {
                            0% {
                                transform: scale(1);
                            }

                            50% {
                                transform: scale(1.2);
                            }

                            100% {
                                transform: scale(1);
                            }
                        }
                    }
                }
            }
        }
    }
}

.swiper {
    max-width: 100%;
   // background: linear-gradient(112deg, #6EEC4E 0%, #FFF 100.41%);

    @media (max-width: 1600px) {
        max-width: 100%;
    }

    .swiper-scrollbar.swiper-scrollbar-horizontal {
        width: 610px;
        height: 8px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 10px;

        @media (max-width: 1440px) {
            bottom: 20px;
        }
        @media (max-width: 1023px) {
            bottom: 10px;
        }
        @media (max-width: 1023px) {
            width: 300px;
        }

    }
    .arrow-block {
     //   display: flex;
       // align-items: center;
        justify-content: center;

    }

    .next {
        position: absolute;
        right: 10%;
        top: 50%;
        transform: translateY(-50%);
        z-index: 10;
        cursor: pointer;

        @media (max-width: 1600px) {
            // top: 20%;
            // transform: translateY(0);
        }
        @media (max-width: 1440px) {
            // top: 15px;
            //  right: 15px;

            svg {
                width: 50px;
                height: 50px;

                @media (max-width: 1023px) {
                    width: 40px;
                    height: 40px;
                }
                @media (max-width: 425px) {
                    width: 30px;
                    height: 30px;
                }
            }
        }
        @media (max-width: 1024px) {
            top: 40px;
            right: 40px;
        }
        @media (max-width: 425px) {
           display: none;
        }
    }
    .prev {
        position: absolute;
        right: 14%;
        top: 50%;

        transform: translateY(-50%) ;
        z-index: 10;
        cursor: pointer;

        @media (max-width: 1600px) {
            // top: 20%;
            // transform: translateY(0);
        }
        @media (max-width: 1440px) {
            // top: 15px;
            //  right: 15px;

            svg {
                width: 50px;
                height: 50px;

                @media (max-width: 1023px) {
                    width: 40px;
                    height: 40px;
                }
                @media (max-width: 425px) {
                    width: 30px;
                    height: 30px;
                }
            }
        }
        @media (max-width: 1024px) {
            top: 40px;
            right: 90px;
        }
        @media (max-width: 425px) {
            display: none;
        }
    }
}




.swiper-slide {
    //display: flex;
    //justify-content: center;
    //align-items: center;
    overflow: hidden;
    padding: 30px 50px 50px 50px;


    @media (max-width: 1600px) {
       // padding: 200px 50px 195px 75px;
    }
    @media (max-width: 1440px) {
      //  padding: 80px 50px 80px 50px;
    }
    @media (max-width: 1023px) {
     //   padding: 30px;
    }
    @media (max-width: 767px) {
       padding: calc(20px + (30 - 20) * ((100vw - 375px) / (767 - 375)));
    }
}

