.case {
    display: flex;
    flex-direction: row;
    gap: 10%;
    justify-content: center;

    @media (max-width: 768px) {
        display: block;
        width: 100%;
    }

    .fishTech-block {
        background-color: var(--color-black);
        border: 9px solid var(--color-black);
        border-radius: 25px;
        overflow: hidden;
        max-height: 500px;
        z-index: 50;
        height: auto;
        width: 100%;
        max-width: 240px;
        display: flex;
        justify-content: center;

        img {
            width: 100%;
            object-fit: cover;
        }

        @media (max-width: 768px) {
            display: none;
        }
    }

    .image {
      //  width: 40%;
        max-height: 500px;
     //   height: auto;
       // margin-right: 6%;
        flex-shrink: 0;
        object-fit: contain;
        filter: drop-shadow(0 35px 25px rgba(0, 0, 0, 0.1));
     //   margin-right: 15px;


        @media (max-width: 768px) {
            display: none;
            max-height: 300px;
        }
    }

    .mobile-block {
        display: flex;
        flex-direction: row;
        flex: 1 1 auto;

        .img-block-mob {
            margin-right: 15px;
        }

      //  margin-right: 15px;
      //  justify-content: flex-start;

        @media (max-width: 768px) {
            margin-bottom: 2.5rem;

        }

        .fishTech-block-mobile {
            display: none;

            @media (max-width: 768px) {
                background-color: var(--color-black);
                border: 5px solid var(--color-black);
                border-radius: 20px;
                overflow: hidden;
                //   max-height: 500px;
                height: auto;
                //  width: 50%;
                max-width: 240px;
                display: flex;
                z-index: 50;
                justify-content: center;
                margin-right: 15px;

                img {
                  //  width: 240px;
                    width: 100%;
                    object-fit: cover;
                }
                .image {
                  //  max-height: 100%;
                    @media (max-width: 500px) {
                      //  max-height: 100%;
                    }
                }

            }


        }
        div {
            .mobile-image {
                display: none;

                @media (max-width: 768px) {
                    display: block;
                    // width: 40%;
                    // margin-right: 1rem;
                }
            }
        }

    }

    .details {
        display: flex;
        flex-direction: column;

        .name {
            padding-top: 0.7rem;
            font-size: 3.15rem;
            font-weight: 700;
            line-height: 3.698rem;
            color: var(--tertiary-color);
            margin-bottom: 1.8rem;
            width: 82.5%;

            @media (max-width: 1024px) {
                width: 100%;
            }

            @media (max-width: 768px) {
                font-size: 1.8rem;
                line-height: 2rem;
                margin-bottom: 2.5rem;
            }
        }

        .description {
            font-size: 1.4rem;
            font-weight: 400;
            line-height: 1.996rem;
            color: var(--dark-color);
          //  margin-bottom: 2.75rem;
            width: 90%;
          //  min-height: 20rem;
            display: flex;
            flex-direction: column;

            p {
                flex: 1 1 auto;
            }

            .description-btn {
                background-color: var(--secondary-color);
                color: var(--light-color);
                font-weight: 600;
                font-size: 0.955rem;
                line-height: 1.121rem;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
                border-radius: 66.32px;
                padding: 10px 30px;
                border: none;
                transition: all 0.3s ease, box-shadow 0.3s ease;
                margin-top: 10px;
                align-self: flex-start;
                margin-bottom: 20px;

                &:hover {
                    background-color: var(--primary-color);
                    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
                }
                @media (max-width: 768px) {
                    margin-bottom: 0;
                    align-self: stretch;
                }
            }

            @media (max-width: 1024px) {
                width: 80%;
            }

            @media (max-width: 480px) {
                font-size: 1.3rem;
                line-height: 1.5rem;
                min-height: 10rem;
                margin-top: 1rem;
                width: 100%;
            }
        }

        .table {
            border: 1px solid var(--dark-color);
            width: 82.5%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 0.6rem 5.5% 0.6rem;
            overflow-x: auto;
            column-gap: 0.5rem;

            @media (max-width: 1440px) {
                width: 100%;
            }

            .column {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                gap: 0.5rem;

                .header {
                    font-size: 1.4rem;
                    font-weight: 400;
                    line-height: 1.6rem;
                    color: var(--dark-color);

                    @media (max-width: 480px) {
                        font-size: 1.2rem;
                        line-height: 1.4rem;
                    }
                }

                .value {
                    font-size: 1.3rem;
                    font-weight: 700;
                    color: var(--dark-color);

                    @media (max-width: 480px) {
                        font-size: 1.1rem;
                    }
                }
            }

            .line-divider {
                height: 3.275rem;
                width: 0.5px;
                background-color: var(--dark-color);
            }
        }
    }
}
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}
.modal-content-case {
    background: #1a1a1a;
    padding: 20px 300px;
   // border-radius: 10px;
    width: 100%;
    color: #fff;
    overflow-y: auto;
    height: 100%;

    h2 {
        font-size: 40px;
        margin-bottom: 25px;
    }
    @media (max-width: 1024px) {
        padding: 20px 150px;
    }
    @media (max-width: 768px) {
        padding: 20px;
    }
}

.img-block {
    display: flex;
    align-items: center;
    justify-content: center;
}

.case-image {
  //  width: 100%;
    max-height: 450px;
    margin-bottom: 20px;
    border-radius: 10px;
}




.modal-details {

    h2 {
        font-size: 28px;
        margin-bottom: 10px;
    }
    p {
        margin-bottom: 20px;
        font-size: 20px;
        line-height: 20px;
    }

    .section {
        margin-bottom: 20px;

        h3 {
            font-size: 20px;
            margin-bottom: 10px;
        }

        ul {
            list-style-type: disc;
            margin-left: 20px;
            li {
                font-size: 18px;

                p {
                    margin-bottom: 15px;
                }

                h4 {
                    font-size: 19px;
                    margin-bottom: 8px;
                }
            }
        }
        ol {

            margin-left: 20px;
            li {
                font-size: 18px;

                p {
                    margin-bottom: 15px;
                }

                h4 {
                    font-size: 19px;
                    margin-bottom: 8px;
                }
            }
        }

    }
}

.close-button-modal-reviw {
    position: absolute;
    top: 15px;
    right: 20px;
    margin-top: 20px;
    padding: 10px 20px;
   // background: #ff7d6a;
   // color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}
